@import 'components/toggles';
@import 'components/timeline';
@import 'node_modules/bootstrap-datepicker/dist/css/bootstrap-datepicker3';

.center-div {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.row-style {
  background-color: #f7f7f7;
  padding: 6px;
  margin: 15px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
}

.tile-style {
  font-size: 85px;
}

.new-user-icon {
  font-size: 96px;
}

.big-icon {
  font-size: 128px;
}

.padded_timeline {
  padding-top: 10px;
}

.inner_padding_right {
  padding-right: 50px;
}

.error_pad_top {
  padding-top: 8%;
}

.center-image {
  display: block;
  margin: 0 auto;
}

.input-group-addon {
  min-width: 42px;
  text-align: center;
}